.box {
    margin: 15px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;

}

.box .box_1 {
    width: 70%;
    display: flex;
    justify-content: space-around;

}

.box .box_2 {
    width: 70%;
    margin-top: 15px;
    display: flex;
    justify-content: space-evenly;

}

.box .box--button {
    border: none;
    font-size: 13px;
    border-radius: 5px;
    height: 30px;
    width: 90px;
    margin-left: -15px;
    font-weight: bold;
    color: white;

    background: #9C27C1;
    background-image: -webkit-linear-gradient(top, #9C27C1, #D757FF);
    background-image: -moz-linear-gradient(top, #9C27C1, #D757FF);
    background-image: -ms-linear-gradient(top, #9C27C1, #D757FF);
    background-image: -o-linear-gradient(top, #9C27C1, #D757FF);
    background-image: linear-gradient(to bottom, #9C27C1, #D757FF);

    font-family: 'Montserrat', sans-serif;

}

.box--button:focus {
    outline: none;

}

.box--button:hover {
    box-shadow: 0px 4px 3px rgba(0, 0, 0, .5);
}